.newPost-container{
    @include flexCenterColumn;
}

.user-image{
padding: 10px;
    img{
        width: 80px;
        height: 80px;
        border-radius: 50px;
        border: $borderBlack;
    }
}

.newPost-form{
    @include flexCenterColumn;
    width: 100%;

}


#text{
    border: $borderBlack;
    max-height: 200px;
    margin-bottom: 10px;
    width: 80%;
}

.futurePost-container{
    display: flex;
    border: $borderBlack;
    width: 95%;
    margin-bottom: 10px;

    .futurePost-left{
        flex-basis: 10%;
        padding: 5px;
        img{
            width: 25px;
            border-radius: 50px;
        }
    }

    .futurePost-right{
        flex-basis: 90%;

        .futurePost-header{
            @include flexSpaceBetween;
            border-bottom: $borderBlack;
            width: 95%;
    
            h3{
                font-size: 12px;
            }
    
            span{
                font-size: 10px;
            }
        }

        .futurePost-content{
            font-size: 12px;
            word-break: break-all;
            @include flexCenterColumn;
            width: 95%;
           
            
            img{
                max-height: 200px;
                max-width: 200px;
                margin-top: 5px;
                margin-bottom: 5px;
            }

            iframe{
                max-height: 90%;
                max-width: 90%;
                margin-top: 5px;
                margin-bottom: 5px;
            }

        }
    } 
}


.futurePost-footer{
    display: flex;
    justify-content: space-between;
    width: 95%;
    margin-bottom: 10px;

    .image-icon{
        @include flexCenter;
        width: 30px;
        padding-left: 30px;
        
            #file-newPost{
                transform: translateX(-25px);
                opacity: 0;
                width: 100%;
                height: 100%;
                cursor: pointer;
            }
        
            i{
                font-size: 20px;
               
                
            }
        }

    .delete-video{
        
        button{
            height: 30px;
            margin-left: 70px;
            width: 130px;
        }

        
    }

    .btn-send{
        display: flex;
    }

    .cancel{
        font-size: 16px;
        font-weight: bold;
    }

    button{
        background-color: $color-1;
        font-size: 12px;
        color: $white;
        height: 30px;
        padding: 0; 
        min-width: 35px;


        &:hover{
            transform: scale(1.06);
        }
    }

    .send{
        background-color: rgb(39, 120, 240);
    }
}

